.link {
  display: inline-block;
  border: 1px $links-border-color solid;
  border-radius: 3rem;
  padding: 1rem 3rem;
  font-size: 1.4rem;
  text-transform: uppercase;
  text-decoration: none;
  font-family: inherit;
  letter-spacing: .1rem;
  color: $links-color;
  cursor: pointer;

  &:visited {
    color: $links-visited-color; }

  &:hover, &:active {
    color: $links-hover-color;
    box-shadow: inset 0 0 10px $white-color; }

  &--header {
    color: $header-links-color;
    border: 1px $header-links-border-color solid;

    &:visited {
      color: $header-links-visited-color; }

    &:hover, &:active {
      color: $header-links-hover-color; } }

  &--active {
    color: $links-visited-color;
    box-shadow: inset 0 0 10px $white-color; } }
