.footer {
  height: 10rem;
  background-color: $dark-grey-color;
  padding: 0 7rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: $main-font;

  &__socialContainer {
    width: 50%;
    display: flex;
    align-items: center; }

  &__rightsContainer {
    width: 50%;
    text-align: end; }

  &__text, &__link {
    font-size: 1.4rem;
    letter-spacing: .1rem;
    font-weight: 300;
    text-transform: uppercase; }

  &__text {
    color: $brownish-grey-color; }

  &__link {
    color: $footer-link-color;
    text-decoration: none;

    &:visited {
      color: $footer-link-visited-color; }

    &:hover, &:active {
      color: $footer-link-hover-color; }

    &:first-of-type {
      margin-left: .5rem; }

    &:not(:first-of-type) {
      margin-left: 2rem; } } }

